*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html,
body {
  height: 100%;
  font-family: "Open Sans", sans-serif;
}

body {
  font-size: 18px;
  line-height: 26px;
}

a {
  text-decoration: none;
  transition: all	.15s ease-in-out;
}

.container {
  max-width: 1280px;
  margin: 0 auto;
  padding: 0 30px;
}

.row {
  margin: 0 -15px;
}

.item {
  padding: 0 15px;
  float: left;
}

#site-header {
  height: 100%;
  text-align: center;
  background: #3E3E58 url("../img/bg/header.jpg") no-repeat center bottom;
  background-size: cover;
  overflow: hidden;
}

#site-header #site-nav {
  position: fixed;
  width: 100%;
  z-index: 1;
  height: 45px;
  padding: 5px 15px;
  background-color: rgba(62, 62, 88, 0.7);
  border-bottom: 1px solid rgba(206, 209, 226, 0.3);
  text-align: left;
  font-size: 12px;
  line-height: 35px;
  font-weight: 600;
  transition: opacity .75s ease;
}

#site-header #site-nav.hidden {
  opacity: 0;
  pointer-events: none;
}

#site-header #site-nav a {
  line-height: 35px;
  display: inline-block;
  color: #fff;
}

#site-header #site-nav a:hover {
  color: #CED1E2;
}

#site-header #site-nav > a {
  color: #fff;
  background: url("../img/icon-mdg.png") no-repeat left center;
  background-size: 24px;
  padding-left: 30px;
}

#site-header #site-nav ul {
  float: right;
}

#site-header #site-nav ul li {
  display: inline-block;
}

#site-header #site-nav ul li::after {
  content: ' | ';
  color: #fff;
}

#site-header #site-nav ul li:last-of-type::after {
  content: '';
}

@media (max-width: 479px) {
  #site-header #site-nav ul li a {
    font-size: 18px;
    font-weight: 700;
    text-transform: uppercase;
    padding: 0 10px;
  }
  #site-header #site-nav ul li a span {
    display: none;
  }
}

#site-header .header-content {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

#site-header .header-content figure div {
  width: 120px;
  height: 120px;
  margin: 0 auto 15px;
  overflow: hidden;
  border-radius: 50%;
  border: 5px solid #3FB8B9;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.5);
}

#site-header .header-content figure div img {
  display: block;
  width: 100%;
}

#site-header .header-content figcaption,
#site-header .header-content h1 {
  color: #fff;
  padding-bottom: 20px;
}

#site-header .header-content figcaption {
  font-size: 20px;
  padding-bottom: 15px;
  display: block;
}

@media (max-width: 479px) {
  #site-header .header-content figcaption {
    font-size: 13px;
    padding-bottom: 5px;
  }
}

#site-header .header-content h1 {
  font-size: 55px;
  margin-top: 10px;
  margin-bottom: 30px;
  font-weight: 700;
}

@media (max-width: 479px) {
  #site-header .header-content h1 {
    font-size: 30px;
    margin-bottom: 15px;
  }
}

#site-header .header-content a {
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  letter-spacing: 2px;
  background: #0D8DC7;
  padding: 15px 30px;
  border-radius: 6px;
}

#site-header .header-content a:hover {
  background: #0a6b97;
}

#site-header::before, #site-header::after {
  background-color: #fff;
  content: '';
  display: block;
  width: calc(50% - 50px);
  height: 40px;
  position: absolute;
  bottom: 0;
}

#site-header::before {
  right: calc(50% + 50px);
}

#site-header::after {
  left: calc(50% + 50px);
}

@media (max-width: 479px) {
  #site-header::before, #site-header::after {
    width: calc(50% - 25px);
    height: 20px;
  }
  #site-header::before {
    right: calc(50% + 25px);
  }
  #site-header::after {
    left: calc(50% + 25px);
  }
}

#main {
  position: relative;
  padding: 40px 15px;
  text-align: center;
  max-width: 600px;
  margin: 0 auto;
}

#main::before {
  content: '';
  display: block;
  width: 100px;
  height: 40px;
  position: absolute;
  left: 50%;
  top: -40px;
  margin-left: -50px;
  background: url("../img/bg/header-arrow.svg") no-repeat center;
  background-size: contain;
}

@media (max-width: 479px) {
  #main::before {
    width: 50px;
    height: 20px;
    top: -20px;
    margin-left: -25px;
  }
}

#projects {
  background: #0D8DC7;
  background-image: linear-gradient(90deg, #3FB8B9 0%, #0D8DC7 100%);
  padding: 60px 0 40px;
}

#projects:before, #projects:after {
  content: " ";
  display: table;
}

#projects:after {
  clear: both;
}

#projects .project {
  width: 33.33333%;
  margin: 40px auto 30px;
}

@media (max-width: 1023px) {
  #projects .project {
    width: 50%;
  }
}

@media (max-width: 767px) {
  #projects .project {
    width: 100%;
    float: none;
    max-width: 420px;
    margin-bottom: 70px;
  }
}

#projects .project .inner {
  background: #fff;
  padding: 20px 20px 80px;
  text-align: center;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.5);
  border-radius: 6px;
  transition: box-shadow .15s ease-in-out;
  position: relative;
}

#projects .project .inner:hover {
  box-shadow: 0 0 0 7px rgba(255, 255, 255, 0.25), 0 0 0 15px rgba(255, 255, 255, 0.25);
}

#projects .project .inner figure {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  overflow: hidden;
  margin: -60px auto 25px;
  background-image: linear-gradient(0deg, #6D80B8 0%, #3E3E58 100%);
  border: 5px solid #CED1E2;
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.25), 0 0 0 7px rgba(255, 255, 255, 0.25);
  transition: box-shadow .15s ease-in-out;
  position: relative;
}

#projects .project .inner figure img {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  opacity: .75;
  transition: opacity .15s ease-in-out;
}

#projects .project .inner h2 {
  font-size: 24px;
  margin-bottom: 30px;
  font-weight: 600;
}

#projects .project .inner p {
  font-size: 16px;
  line-height: 24px;
  margin-bottom: 30px;
}

#projects .project .inner a {
  display: inline-block;
  color: #fff;
  text-transform: uppercase;
  white-space: nowrap;
  font-weight: 600;
  letter-spacing: 2px;
  background: #3FB8B9;
  padding: 6px 30px;
  border-radius: 6px;
  box-shadow: 0 0 0 0 #329293;
  transition: all .15s ease-in-out;
  position: absolute;
  bottom: 30px;
  transform: translateX(-50%);
}

#projects .project .inner a:hover {
  border-radius: 3px;
  box-shadow: 0 0 0 3px #329293;
  background: #329293;
}

#projects .project .inner:hover figure {
  box-shadow: inset 0 0 5px rgba(0, 0, 0, 0.25), 0 0 0 7px rgba(255, 255, 255, 0.25), 0 0 0 15px rgba(255, 255, 255, 0.25);
}

#projects .project .inner:hover figure img {
  opacity: 1;
}

.extras {
  padding: 40px 30px;
}

.extras p {
  text-align: center;
  margin-bottom: 20px;
}

.extras ul {
  column-count: 2;
  column-gap: 30px;
  max-width: 450px;
  margin: 0 auto;
}

@media (max-width: 767px) {
  .extras ul {
    column-count: 1;
    text-align: center;
  }
}

.extras ul li {
  font-size: 14px;
}

.extras ul li a {
  color: #0D8DC7;
}

.extras ul li a:hover {
  color: #3FB8B9;
}

footer {
  background-color: #3E3E58;
  padding: 70px 0;
}

@media (max-width: 767px) {
  footer {
    padding: 40px 0;
  }
}

@media (max-width: 479px) {
  footer {
    padding: 30px 0;
  }
}

footer:before, footer:after {
  content: " ";
  display: table;
}

footer:after {
  clear: both;
}

footer .info {
  text-align: right;
  width: 80%;
}

@media (max-width: 767px) {
  footer .info {
    width: 100%;
    text-align: center;
    padding-bottom: 20px;
  }
}

footer .info p {
  font-size: 14px;
  line-height: 18px;
  color: #CED1E2;
  font-weight: 300;
}

footer .info p a {
  color: #CED1E2;
}

footer .info p a:hover {
  color: #fff;
}

footer .social {
  width: 20%;
}

@media (max-width: 767px) {
  footer .social {
    width: 100%;
    text-align: center;
  }
}

footer .social ul {
  list-style-type: none;
  margin-top: 3px;
}

footer .social ul li {
  display: inline-block;
  margin-right: 5px;
}

footer .social ul li:last-of-type {
  margin-right: 0;
}

footer .social ul li a svg:hover .social-icon {
  fill: #fff;
}

footer .social ul li a svg .social-icon {
  transition: fill .3s ease-in-out;
  fill: #CED1E2;
}
